html {
  font-size: 12px;
  font-family: Helvetica,STHeiti,Microsoft YaHei,Verdana,Arial,Tahoma,sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
/*
 * A标签设置
 */
a {
  cursor: pointer;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  outline: medium;
}
a,
a:focus,
a:hover {
    color: #38f;
    text-decoration: none;
}
a:active,
a:focus {
  outline: none;
}
b,
strong {
  font-weight: bold;
}
mark {
  background: #ff0;
  color: #000;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
pre {
  overflow: auto;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
input,
button,
textarea,
select,
optgroup,
option {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  font-style: inherit;
  margin: 0;
  padding: 0;
  vertical-align: middle;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
ul,
li {
  list-style: none;
}
.clear {
  clear: both;
}
.clearoflow {
  overflow: hidden;
  zoom: 1;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  zoom: 1;
}
/*
    图片设置
 */
.img {
  max-width: 100%;
  height: auto;
  border: none;
}
.pull-left{
  float: left;
}
.pull-right{
