.btn{
  display: inline-block;
  height: 30px;
  line-height: 30px;
  padding: 0 10px;
  border-radius: 2px;
  font-size: 12px;
  color: #333;
  background: #fff;
  border: 1px solid #bbb;
  text-align: center;
  vertical-align: middle;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  cursor: pointer;
  -webkit-transition: background-color .3s;
  transition: background-color .3s;
}
.btn:focus {
    outline: none
}
.btn:focus,
.btn:link,
.btn:visited {
    color: #333;
    text-decoration: none
}
.btn:hover {
    color: #59f;
    border-color: #59f;
    text-decoration: none
}
.btn:active {
    color: #333;
    background: #e5e5e5;
    border-color: #bbb;
    text-decoration: none
}
.btn-primary {
    color: #fff;
    background: #38f;
    border-color: #38f
}
.btn-primary:focus,
.btn-primary:link,
.btn-primary:visited {
    color: #fff
}
.btn-primary:hover {
    color: #fff;
    background: #59f;
    border-color: #59f
}
.btn-primary:active {
    color: #fff;
    background: #27f;
    border-color: #27f
}
.btn-danger {
    color: #fff;
    background: #f44;
    border-color: #f44
}
.btn-danger:focus,
.btn-danger:link,
.btn-danger:visited {
    color: #fff
}
.btn-danger:hover {
    color: #fff;
    background: #f66;
    border-color: #f66
}
.btn-danger:active {
    color: #fff;
    background: #e33;
    border-color: #e33
}
.btn-success {
    color: #fff;
    background: #4b0;
    border-color: #4b0
}
.btn-success:focus,
.btn-success:link,
.btn-success:visited {
    color: #fff
}
.btn-success:hover {
    color: #fff;
    background: #6c2;
    border-color: #6c2
}
.btn-success:active {
    color: #fff;
    background: #0a0;
    border-color: #0a0
}
.btn-disabled,
.btn-disabled[disabled] {
    color: #bbb;
    background: #f8f8f8;
    border-color: #e5e5e5;
    cursor: not-allowed
}
.btn-disabled:focus,
.btn-disabled:link,
.btn-disabled:visited,
.btn-disabled[disabled]:focus,
.btn-disabled[disabled]:link,
.btn-disabled[disabled]:visited {
    color: #bbb
}
.btn-disabled:active,
.btn-disabled:hover,
.btn-disabled[disabled]:active,
.btn-disabled[disabled]:hover {
    color: #bbb;
    background: #f8f8f8;
    border-color: #e5e5e5
}
input[type='text']{
  display: inline-block;
  height: 28px;
  padding: 4px 6px;
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 20px;
  color: #555;
  vertical-align: middle;
  border-radius: 4px;
  background-color: #fff;
  border: 1px solid #ccc;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
  -webkit-transition: border linear .2s,box-shadow linear .2s;
  -webkit-transition: border linear .2s,-webkit-box-shadow linear .2s;
  transition: border linear .2s,-webkit-box-shadow linear .2s;
  transition: border linear .2s,box-shadow linear .2s;
  transition: border linear .2s,box-shadow linear .2s,-webkit-box-shadow linear .2s
}
